export default "@import '../../themes/src/default.css';\n\n:root {\n    /* misc */\n    --button-font-family: var(--font-family);\n    --button-font-weight: 500;\n\n    /* border-radius */\n    --button-xxs-size-border-radius: 6px;\n    --button-xs-size-border-radius: var(--border-radius-m);\n    --button-s-size-border-radius: var(--border-radius-m);\n    --button-m-size-border-radius: var(--border-radius-l);\n    --button-l-size-border-radius: var(--border-radius-l);\n    --button-xl-size-border-radius: var(--border-radius-l);\n}\n\n.component {\n    position: relative;\n    display: inline-flex;\n    vertical-align: middle;\n    flex-direction: row;\n    flex-wrap: nowrap;\n    justify-content: center;\n    align-content: center;\n    align-items: center;\n    margin: 0;\n    padding: 0 23px;\n    line-height: 20px;\n    font-weight: var(--button-font-weight);\n    font-family: var(--font-family);\n    text-align: center;\n    text-decoration: none;\n    background-color: transparent;\n    border: 0;\n    outline: none;\n    box-shadow: none;\n    user-select: none;\n    cursor: pointer;\n    transition: background 0.2s ease, border 0.2s ease, color 0.2s ease, transform 0.12s ease;\n    box-sizing: border-box;\n    will-change: transform;\n}\n\n.focused {\n    @mixin focus-outline;\n}\n\n.loading .text,\n.loading .addons {\n    opacity: 0;\n}\n\n.stretchText {\n    flex-grow: 1;\n}\n\n.loader {\n    position: absolute;\n    left: 0;\n    right: 0;\n    margin: 0 auto;\n}\n\n/* Size */\n\n/* За счет того, что у кнопок есть border 1px,\nдля полного соответствия макетам padding необходимо уменьшить на 1px (16px -> 15px) */\n.xxs {\n    min-width: 80px;\n    min-height: var(--size-xxs-height);\n    padding: 0 15px;\n    font-size: 14px;\n    border-radius: var(--button-xxs-size-border-radius);\n\n    &.iconOnly {\n        min-width: var(--size-xxs-height);\n    }\n}\n\n.xs {\n    min-width: 88px;\n    min-height: var(--size-xs-height);\n    padding: 0 19px;\n    font-size: 14px;\n    border-radius: var(--button-xs-size-border-radius);\n\n    &.iconOnly {\n        min-width: var(--size-xs-height);\n    }\n}\n\n.s {\n    min-width: 104px;\n    min-height: var(--size-s-height);\n    font-size: 16px;\n    line-height: 24px;\n    border-radius: var(--button-s-size-border-radius);\n\n    &.iconOnly {\n        min-width: var(--size-s-height);\n    }\n}\n\n.m {\n    min-width: 128px;\n    min-height: var(--size-m-height);\n    padding: 0 27px;\n    font-size: 16px;\n    line-height: 24px;\n    border-radius: var(--button-m-size-border-radius);\n\n    &.iconOnly {\n        min-width: var(--size-m-height);\n    }\n}\n\n.l {\n    min-width: 160px;\n    min-height: var(--size-l-height);\n    padding: 0 31px;\n    font-size: 18px;\n    line-height: 24px;\n    border-radius: var(--button-l-size-border-radius);\n\n    &.iconOnly {\n        min-width: var(--size-l-height);\n    }\n}\n\n.xl {\n    min-width: 168px;\n    min-height: var(--size-xl-height);\n    padding: 0 35px;\n    font-size: 18px;\n    line-height: 24px;\n    border-radius: var(--button-xl-size-border-radius);\n\n    &.iconOnly {\n        min-width: var(--size-xl-height);\n    }\n}\n\n.withRightAddons {\n    &.xxs {\n        padding-right: 11px;\n    }\n\n    &.xs {\n        padding-right: 15px;\n    }\n\n    &.s {\n        padding-right: 19px;\n    }\n\n    &.m {\n        padding-right: 23px;\n    }\n\n    &.l {\n        padding-right: 27px;\n    }\n\n    &.xl {\n        padding-right: 31px;\n    }\n\n    &.ghost {\n        padding-right: 0;\n    }\n}\n\n.withLeftAddons {\n    &.xxs {\n        padding-left: 11px;\n    }\n\n    &.xs {\n        padding-left: 15px;\n    }\n\n    &.s {\n        padding-left: 19px;\n    }\n\n    &.m {\n        padding-left: 23px;\n    }\n\n    &.l {\n        padding-left: 27px;\n    }\n\n    &.xl {\n        padding-left: 31px;\n    }\n\n    &.ghost {\n        padding-left: 0;\n    }\n}\n\n.link {\n    min-width: 0;\n}\n\n.ghost {\n    min-width: 0;\n    min-height: 0;\n    padding: 0;\n    border: none;\n    appearance: none;\n\n    &.iconOnly {\n        min-width: 0;\n    }\n}\n\n/* Disabled */\n\n.component[disabled] {\n    cursor: var(--disabled-cursor);\n\n    & * {\n        pointer-events: none;\n    }\n}\n\n/* Addons */\n\n.addons {\n    display: flex;\n    flex-shrink: 0;\n    align-items: center;\n    margin: 0;\n\n    &:first-child {\n        margin-right: var(--gap-2xs);\n    }\n\n    &:last-child {\n        margin-left: var(--gap-2xs);\n    }\n\n    &:only-child {\n        margin: 0;\n    }\n}\n\n.s,\n.m {\n    & .addons:first-child {\n        margin-right: var(--gap-xs);\n    }\n\n    & .addons:last-child {\n        margin-left: var(--gap-xs);\n    }\n\n    & .addons:only-child {\n        margin: 0;\n    }\n}\n\n.l,\n.xl {\n    & .addons:first-child {\n        margin-right: var(--gap-s);\n    }\n\n    & .addons:last-child {\n        margin-left: var(--gap-s);\n    }\n\n    & .addons:only-child {\n        margin: 0;\n    }\n}\n\n.iconOnly {\n    padding: 0;\n}\n\n/* Block */\n\n.block {\n    display: flex;\n    width: 100%;\n}\n\n/* Nowrap */\n\n.nowrap {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n/* IE min-height fix */\n\n@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n    .component:after {\n        min-height: inherit;\n        font-size: 0;\n        content: '';\n    }\n}\n";